<template>
  <div class="test mt-5">  

    <img class="back" src="../assets/back.png" @click="back">
    <mavon-editor 
     class="markDwonPart"
     :value="articleDetail.technology"
     :subfield = "prop.subfield"
     :defaultOpen = "prop.defaultOpen"
     :toolbarsFlag = "prop.toolbarsFlag"
     :editable="prop.editable"
     :scrollStyle="prop.scrollStyle"
     v-show="!isVisable"/>

    <b-row class="mt-5" v-show="isVisable">
      <b-col cols="18" md="12" class="mt-5">
          <div class="d-flex justify-content-center mt-5">
            <b-spinner label="Loading..."></b-spinner>
          </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: 'test',
  data(){
    return {
      articleDetail: '',
      isVisable:true,
    }
  },
  mounted () {
    var api = `https://www.domyown.top/ssm/getDetail/7`
    this.axios
      .get(api)
      .then(response => {
        this.isVisable = false
        this.articleDetail = response.data.extend.detailsInfo
      })
      .catch(error => (console.log(error)))
  },
  computed: {
      prop () {
        let data = {
          subfield: false,// 单双栏模式
          defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域 
          editable: false,
          toolbarsFlag: false,
          scrollStyle: false,
        }
        return data
      }
    },
  methods: {
    back () {
       this.$router.push({ path: '/'});
    }
  }
}
</script>

<style lang="less" scoped>
.test{
  max-width: 1500px;
  margin: 0px auto;
}

.markDwonPart{
  max-width: 1200px;
  margin: 0px auto;
  z-index: 1;
}
.back{
  position: fixed;
  right: 50px;
  bottom: 90px;
  border: 1px #585858 solid;
  border-radius: 50%;
  padding: 5px;
  background: #fff;
  z-index: 9999;
  width: 45px;
  height: 45px;
  cursor: pointer;
}
</style>
